@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: 'Poppins' , -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0px;
  padding: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.app{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 24px;
}

.app__searchForm {
  margin-top: 18px;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.app__healthLabels{
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
  
}

.app__recipes{
  margin-top: 24px;
  display: grid;
  grid-template-columns: 230px 230px;
  align-items: baseline;
}
.recipeTile__img {
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
}

.recipeTile{
    cursor: pointer;
}

.recipeTile__name{
    max-width: 200px;
    text-align: center;
    margin: 12px 0px;
}
